.modal-buy {
    position: fixed;
    margin-left: auto;
    margin-right: auto;
    z-index: 100;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 1;
    @include transitionM(opacity);
    @include transitionM(visibility);
}

.modal-buy.is-hidden-buy-now {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
}

.modal-buy.is-hidden-buy-now .order {
    transform: translate(-50%, -50%) scale(1.1);
}

.form-buy-modal {
    position: absolute;
    z-index: 100;
    display: flex;
    justify-content: right;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    padding: 28px;
    background-color: $main-text-color;
    border-radius: 10px;
    @include transitionM(transform);
    border: 2px solid $accent-color;
    width: 280px;
    height: 95%;
    @include mq (tab) {
        min-width: 600px;
        height: auto;
    }


}

.form-buy-modal__button {
    position: absolute;
    display: inline-flex;
    top: 12px;
    right: 12px;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border: none;
    
    color: $second-text-color;
    background-color: transparent;
    @include transitionM(color);

    &:hover,
    &:focus {
        color: $button-color;
    }
}

.form-buy-modal__icon {
    fill: currentColor;
    flex-grow: 0;
    width: 24px;
        height: 24px;
}

.order {
    width: 200px;
    margin: 0 auto;
    height: 95%;
    overflow: auto;
    @include mq(tab) {
        width: 500px;
        height: auto;
        margin: 0 auto;
    }
}

.order__title {
    margin-bottom: 83px;
    font-size: 16px;
    line-height: calc(24/16);
    font-family: $font-family-title;
    text-align: center;
    color: $second-text-color;
    @include mq (tab) {
        
        font-size: 20px;
        line-height: calc(28/20);
    }
}

.order__input {
    padding: 8px 12px;
    width: 100%;
    height: 32px;
    border-radius: 10px;
    outline: none;
    font-size: 12px;
    color: $second-text-color;
    background-color: rgba(248, 210, 78, 0.1);
    @include transitionM(border);
    border: 1px solid $main-background-color;
    &:focus {
        border-color: $accent-color;
    }
    &::placeholder {
        color: $second-text-color;
        font-size: 12px;
        @include mq(tab) {
            font-size: 14px;
        }
    }
    @include mq(tab) {
        padding: 11px 16px;
        }
}



.order__field {
    position: relative;
    margin-bottom: 12px;
    color: $second-text-color;

    @include mq (tab)  {
        margin-bottom: 16px;
    }
}

.order__field--margin {
    margin-bottom: 24px;
    @include mq (tab) {
            margin-bottom: 32px;
        }
}


.order__feedback {
    display: block;
    font-family: $font-family;
    padding: 8px 12px;
    width: 100%;
    color: $second-text-color;
    height: 84px;
    font-size: 12px;
    resize: none;
    border: 1px solid $main-background-color;
    border-radius: 10px;
    outline: none;
    background-color: rgba(248, 210, 78, 0.1);
    @include transitionM(border);

    @include mq(tab) {
        padding: 11px 16px;
        }
    
    
    &:focus {
        border-color: $accent-color;
    }
    &::placeholder {
            color: $second-text-color;
            font-size: 12px;
    
            @include mq(tab) {
                font-size: 14px;
            }
        }
}

.btn-buy-now {
    display: block;
    margin: 0 auto;
}

.order__form {
    max-width: 368px;
    margin: 0 auto;
}

.order-products {
    width: 132px;
    margin: 0 auto;
    @include mq(tab) {
        display: flex;
        gap: 22px;
        width: 100%;
        margin-bottom: 40px;
        margin-top: 103px;
        justify-content: center;
        align-items: center;
    }
}

.img-modal__products-one, .img-modal__products-second, .img-modal__products-third {
    position: absolute;
    top: -120%;
    transform: translateY(50%);
    width: 180px;
    left: -18%;
}

.product-one, .product-second, .product-third {
    width: 180px;
    height: auto;
}

.product__icecream {
    font-weight: 700;
    font-size: 16px;
    line-height: calc(20/16);
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $second-text-color;
    width: 132px;
    height: 136px;
    border: 2px solid $main-background-color;
    background-color: transparent;
    border-radius: 16px;
    padding-top: 96px;
    &--second {
        background-color: $card-second-color;
        border: 2px solid $card-second-color;
    }
    &--third {
        border: 2px solid $card-third-color;
    }

}

.products-modal-one, .products-modal-second, .products-modal-third {
    position: relative;
    
}

.products-modal-one,
.products-modal-second {
    margin-bottom: 83px;
    @include mq(tab) {
        margin-bottom: 0;
        }
}

.products-modal-third {
    margin-bottom: 28px;
    @include mq(tab) {
            margin-bottom: 0;
        }
}